import { Component, OnInit } from '@angular/core';
import { testData } from "app/showcase/custom-components/ui-grid-demo/data";

@Component({
  selector: 'ui-grid-demo-object',
  template: `
  <ui-grid
      [columns]="columns"
      [data]="data"
      (load)="load($event)"
      [selections]="selections"
      [enableExport]="enableExport"
      [showEditColumn]="showEditColumn"
      (cellMouseEnter)="cellMouseEnter($event,op,op2,op3)"
      (cellMouseLeave)="cellMouseLeave($event,op,op2,op3)"
      (rowSelect)="rowSelect($event)"
  ></ui-grid>
  
<!--使用p-dataTabel组件定义内容-->
<p-overlayPanel #op2 [showCloseIcon]="true" [dismissable]="false">
<h3>使用prime-ng组件p-dataTable自定义表格展示</h3>
<p-dataTable [value]="op2Data" [style]="{'width':'500px'}">
    <p-column field="vin" header="Vin" [sortable]="true"></p-column>
    <p-column field="year" header="Year" [sortable]="true"></p-column>
    <p-column field="brand" header="Brand" [sortable]="true"></p-column>
    <p-column field="color" header="Color" [sortable]="true"></p-column>
</p-dataTable>
</p-overlayPanel>

<!--自定义浮动窗口的时候必须像以下格式-->
<p-overlayPanel #op [showCloseIcon]="true" [dismissable]="false">
<div style="width: 300px;">
    <h3>自定义弹出内容</h3>
    <div style="word-wrap:break-word;">
        {{cellOverEvent}}
    </div>
</div>
</p-overlayPanel>
<!--使用ui-grid组件定义内容-->
<p-overlayPanel #op3 [showCloseIcon]="true" [dismissable]="false">
<div style="width: 500px;overflow: scroll">
    <h3>使用ui-grid组件自定义表格展示</h3>
    <ui-grid [columns]="overTableColumns"
             [data]="overTableData"
             [enableExport]="enableExport"
             [showEditColumn]="showEditColumn"
             (load)="overTableload($event)"
    ></ui-grid>
</div>
</p-overlayPanel>
  `,
  // styles: [
  //   `:host ::ng-deep .ui-overlaypanel {
  //     position: fixed;
  // }`
  // ]
})
export class UIGridDemoObjectComponent implements OnInit {
  columns: any[] = [];
  selections: any = [];
  data = {};

  selectedRow: any[];

  //overflow win
  op2Data: any = [];
  overTableColumns: any[] = [];
  overTableData: any;
  cellOverEvent: any;
  enableExport: boolean = false;
  showEditColumn: boolean = false;

  constructor() { }

  ngOnInit() {
    this.initColumns();
    this.initOverTableColumns();
    this.overTableload();
  }

  load($event) {
    /**
     *  test getting data
     */
    this.data = testData;
  }

  rowSelect($event) {
    this.selectedRow = $event;
  }
  /**
       * 表格字段鼠标进入表格cell事件触发回调方法
       * @param $event
       * @param restObj 浮动窗口对象，根据窗口数量传参自己定义，因为此处举例三个弹窗
       */
  cellMouseEnter($event, ...restObj: any[]): any {
    this.cellOverEvent = JSON.stringify($event);
    overlayPanelShow($event, restObj, ['id', "code", "name"]);

  }

  /**
   * 鼠标从自定义悬浮框上移走触发
   * @param restObj
   */
  cellMouseLeave($event, ...restObj: any[]) {

    // overlayPanelHide($event, restObj, ['id', "code", "name"]);

  }
  /**
     * 浮动弹出表格
     */
  initOverTableColumns() {
    let columns = [
      {
        field: "id",
        header: "ID",
        sortable: true
      },
      {
        field: "product",
        header: "产品",
        sortable: true
      },
      {
        field: "installCount",
        header: "安装数量",
        sortable: true
      },
      {
        field: "packageCount",
        header: "打包数量",
        sortable: true
      }, {
        field: "weight",
        header: "总重量",
        sortable: true
      }
    ]
    this.overTableColumns = columns;
  }

  overTableload($event?) {
    //测试数据
    this.overTableData = {
      content: [{
        "id": 1,
        "product": '沙发',
        "installCount": '12',
        "packageCount": '12',
        "weight": '23kg'

      }, {
        "id": 2,
        "product": '玻璃门',
        "installCount": '3',
        "packageCount": '3',
        "weight": '50kg'

      }],
      first: true,
      last: true,
      number: 0,
      numberOfElements: 4,
      size: 20,
      totalElements: 4,
      totalPages: 1
    }


    this.op2Data = [
      { "brand": "VW", "year": 2012, "color": "Orange", "vin": "dsad231ff" },
      { "brand": "Audi", "year": 2011, "color": "Black", "vin": "gwregre345" },
      { "brand": "Renault", "year": 2005, "color": "Gray", "vin": "h354htr" },
      { "brand": "BMW", "year": 2003, "color": "Blue", "vin": "j6w54qgh" },
      { "brand": "Mercedes", "year": 1995, "color": "Orange", "vin": "hrtwy34" },
      { "brand": "Volvo", "year": 2005, "color": "Black", "vin": "jejtyj" },
      { "brand": "Honda", "year": 2012, "color": "Yellow", "vin": "g43gr" },
      { "brand": "Jaguar", "year": 2013, "color": "Orange", "vin": "greg34" },
      { "brand": "Ford", "year": 2000, "color": "Black", "vin": "h54hw5" },
      { "brand": "Fiat", "year": 2013, "color": "Red", "vin": "245t2s" }
    ];
  }
  initColumns() {
    this.columns = [{
      field: "id",
      header: "ID",
      filter: true
    }, {
      field: "picture",
      header: "缩略图",
      width: '80px',
      thumbnail: true /**控制缩略图字段 */
    }, {
      field: "code",
      header: "代号",
      width: "100px",
      defaultTipsHidden: true
    }, {
      field: "name",
      header: "名称",
      hidden: false,
      isWarn: true /* 判断字段是否为0，一般仅用在金额为0时，变成红色，配合下面rowSelect方法可以使金额为0的行不可选中*/
    }, {
      field: "hidden",
      header: "隐藏的列",
      hidden: true,
      filter: true
    }, {
      field: "fullName",
      header: "全称",
      textLength: 11 /*该字段鼠标移动会有悬浮显示隐藏文字*/
    }, {
      field: "userJzts[0].accountName",
      header: "子VO列",
      sortable: true
    }];
  }

}


/**
 * Created by giscafer on 2017-05-16.
 */

/**
 * 表格字段鼠标进入表格cell事件触发回调方法
 * @param $event
 * @param layArr p-overlayPanel对象数组，浮动窗口对象，根据窗口数量传参自己定义，因为此处举例三个弹窗
 * @param fieldNames 元素值的位置要和layArr对应元素一致
 */
export function overlayPanelShow($event, layArr: any[], fieldNames: string[]): any {

  let field = $event.field;
  let index = fieldNames.indexOf(field);
  if (index !== -1) {
    layArr[index].show($event.originalEvent);
  }
}

/**
 * 鼠标从自定义悬浮框上移走触发
 * @param layArr  p-overlayPanel对象数组
 */
export function overlayPanelHide($event, layArr: any[], fieldNames: any[]) {
  let field = $event.field;
  let index = fieldNames.indexOf(field);
  let relatedTarget = $event.originalEvent.relatedTarget || {};
  if (index !== -1 && !containsOverLayer(relatedTarget.className)) {
    layArr[index].hide();
  } else if (index !== -1) {
    //离开弹窗自动隐藏
    let nativeEle = layArr[index].el.nativeElement;
    if (!nativeEle.children[0]) return;
    nativeEle.children[0].onmouseleave = function () {
      layArr[index].hide();
    }
  }
}

/**
 * 判断类名是否存在浮动框，控制鼠标在overlay之上不隐藏弹窗
 * @param className
 * @returns {any}
 */
function containsOverLayer(className) {
  if (!className) return false;
  return className.includes('ui-overlaypanel');
}




