import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { UIGridDemoComponent } from "app/showcase/custom-components/ui-grid-demo/ui-grid-demo.component";
import { NzCodeBoxModule } from "app/showcase/share/nz-codebox/nz-codebox.module";
import { UIGridDemoRoutingModule } from "app/showcase/custom-components/ui-grid-demo/ui-grid-demo.routing.module";
import { NzGridModule } from "app/showcase/share/nz-grid/nz-grid.module";
import { NzToolTipModule } from "app/showcase/share/nz-tooltip/nz-tooltip.module";
import { UIGridDemoBasicComponent } from "app/showcase/custom-components/ui-grid-demo/ui-grid-demo-basic.component";
import { UIGridDemoObjectComponent } from "app/showcase/custom-components/ui-grid-demo/ui-grid-demo-object.component";
import { UIGridModule } from "app/custom-components/custom-components.module";
import { OverlayPanelModule } from "app/components/overlaypanel/overlaypanel";
import { DataTableModule } from "app/components/datatable/datatable";

@NgModule({
	declarations:[
		UIGridDemoComponent,
		UIGridDemoBasicComponent,
		UIGridDemoObjectComponent
	],
	imports: [
		UIGridDemoRoutingModule,
		CommonModule,
		FormsModule,
		NzGridModule,
		NzToolTipModule,
		NzCodeBoxModule,
		UIGridModule,
		OverlayPanelModule,
		DataTableModule
	]
})
export class UIGridDemoModule { }



