import { Component, OnInit } from '@angular/core';
import { testData } from "app/showcase/custom-components/ui-grid-demo/data";

@Component({
  selector: 'ui-grid-demo-basic',
  template: `
  <ui-grid
      [columns]="columns"
      [data]="data"
      (load)="load($event)"
      (cellClick)="cellClick($event)"
      [selections]="selections"
      (rowSelect)="rowSelect($event)"
  ></ui-grid>
  `,
  styles: []
})
export class UIGridDemoBasicComponent implements OnInit {

  columns: any[] = [];
  selections: any = [];
  data = {};

  selectedRow:any[];

  constructor() { }

  ngOnInit() {
    this.initColumns();
    
  }

  load($event){
    /**
     *  test getting data
     */
    this.data=testData;
  }

  rowSelect($event){
    this.selectedRow=$event;
  }

  cellClick($event){
    alert(JSON.stringify($event))
  }

  initColumns() {
    this.columns = [{
      field: "id",
      header: "ID",
      filter: true
    }, {
      field: "picture",
      header: "缩略图",
      width: '80px',
      thumbnail: true /**控制缩略图字段 */
    }, {
      field: "code",
      header: "代号",
      width: "100px",
    }, {
      field: "name",
      header: "名称",
      hidden: false,
      isWarn: true /* 判断字段是否为0，一般仅用在金额为0时，变成红色，配合下面rowSelect方法可以使金额为0的行不可选中*/
    }, {
      field: "hidden",
      header: "隐藏的列",
      hidden: true,
      filter: true
    }, {
      field: "fullName",
      header: "全称",
      textLength: 11 /*该字段鼠标移动会有悬浮显示隐藏文字*/
    }, {
      field: "userJzts[0].accountName",
      header: "子VO列",
      link:true, /*该字段有点击交互*/
      sortable: true
    }];
  }

}



